header{

  .topNav{
    background-color: var(--navyblue);
    color: white;
    display:flex;
    flex-direction: row;
    justify-content: end;
    align-items: center;
    max-height: 10%;
    width: 100%;

    ul {
      display: flex;
      font-size: 15px;
      color: white;
      font-style: italic;
      width: 40%;
      height: 10%;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-right: 10px;
      padding-left: 10px;
      list-style-type: none;
      letter-spacing: 0.1em;
      justify-content: center;
      gap: 40px;
      align-items: center;
      text-shadow: 2px 2px 4px rgba(10, 0, 0, 0.986);
    }

    a:hover {
      color: white;
      font-weight: bolder;
      cursor: pointer;
      align-items: center;
      text-align: center;
      // text-shadow: 2px 2px 4px rgba(255, 255, 255, 1);
      text-shadow: none;
      transition: font-weight 0.2s ease-in-out;
    }
    
  }

  .mainNav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    max-height: 20%;
    width: 100%;
    padding-top: 5px;
    position: fixed;
    text-align: center;
    z-index: 60;

    ul {
      font-size: 22px;
      text-shadow: 4px 4px 6px rgba(0, 0, 0, 1.7);
      display: flex;
      cursor: pointer;
      color: white;
      list-style-type: none;
      letter-spacing: 0.1em;
      justify-content: space-evenly;
      padding: 10px;
      gap: 50px;
      align-items: center;
      height: 15%;
      width: 90%;

      @media (max-width: 1600px) {
        gap: 20px;
      }

      @media (max-width: 1200px) {
        width: 90%;
      }
    }

    

    ul a:hover {
      color: var(--navyblue);
      font-weight: bolder;
      cursor: pointer;
      align-items: center;
      text-align: center;
      // text-shadow: 2px 2px 4px rgba(255, 255, 255, 1);
      text-shadow: none;
      transition: font-weight 0.2s ease-in-out;
    }

    li {
      
      padding: 20px;
    }

    li:last-child a {
      border: 2px solid white; 
      border-color: white;
      background-color: none;
      padding: 20px 20px;
      border-radius: 5px;
    }

    li:last-child a:hover {
      border-color: var(--navyblue);
    }
    

    .accountButton {
      display: flex;
      flex: column;
      background-color: transparent;
      color: white;
      font-weight: bold;
      font-size: 20px;
      letter-spacing: 0.1em;
      border: none;
      height: 70px;
      width: 170px;
      cursor: pointer;
      text-align: center;
      padding-top: 12px;
      transition: 0.1s;
      padding-bottom: 40px;
      transition: color 0.2s ease;
      transition: text-shadow 0.2s ease;
      justify-content: center;
      gap: 10px;
    }
    // @media screen and (max-width:600px) {
        
    //     flex-direction: column
    // }
  }
  
}

